.app {
  height: 100%;
  margin-top: 5px;
  display:flex;
  justify-content:center;
  background-size:cover;

}

.todo-list {
  background: lightsalmon;
  border-radius: 4px;
  padding: 5px;
  max-width: 800px;
 
}

.todo {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 3px 10px;
  font-size: 14px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family:Arial, Helvetica, sans-serif;
}
.space{
  margin-left: 5px;
}
.complete:hover{
  color:#00aa2b;
}
.remove:hover{
  color:red;
}
h1{
  text-align: center;
  font-family:Arial, Helvetica, sans-serif;
  background-color: rgb(43, 43, 43);
  padding: 25px;
  color:lightsalmon;
  font-size:50px;
}
html,body{
  margin:0;
  background-color:rgb(43, 43, 43);
  font-family:Arial, Helvetica, sans-serif;
  color: rgb(43, 43, 43);
 }

 .task{
   text-align:center;
   margin-top:10px;
   color:lightsalmon;
 }
 footer{
   color: lightsalmon;
   padding:500px 0 0 0;
   text-align: center;
 }